<template>
  <div id="app">

    <nav v-if="this.$route.meta.showNav">
      <router-link to="/">
        <img src="./assets/图标/icon_home.png" alt="">
        <img src="./assets/图标/icon_home_selected.png" alt="">
      </router-link>
      <router-link to="/find">
        <img src="./assets/图标/icon_dicover.png" alt="">
        <img src="./assets/图标/icon_discover_selected.png" alt="">
      </router-link>
      <router-link to="/serialize">
        <img src="./assets/图标/icon_serial.png" alt="">
        <img src="./assets/图标/icon_serial_selected.png" alt="">
      </router-link>
      <router-link to="/mine">
        <img src="./assets/图标/icon_me.png" alt="">
        <img src="./assets/图标/icon_me_selected.png" alt="">
      </router-link>
    </nav>
    <keep-alive>
      <transition name="animate__animated animate__bounce" enter-active-class="animate__fadeIn"
      leave-active-class="animate__fadeOut">
      <router-view />
      </transition>
    </keep-alive>
    <audio :src=acticle?.json_content?.radio_url autoplay ref="audio" @play="$store.commit('changePlay')"
      @pause="$store.commit('changePlayf')" @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"></audio>
    <img class="playbar" @click="showplay=!showplay" src="./assets/图标/float_player_pause.png" alt="">
    <PlayBar v-if="showplay" @change-show="showplay=!showplay" :title="acticle?.json_content?.title"
      :currentTime="currentTime" :duration="duration" @update-time="updatetime"
      :author="acticle?.author_list[0]?.user_name" :acticle="acticle" />
  </div>
</template>
<script>
import { mapState } from "vuex";
import PlayBar from '@/components/PlayBar.vue'
export default {
  components: {
    PlayBar
  },
  data() {
    return {
      // song: "",
      duration: 0,
      currentTime: 0,
      showplay: false,
      // JSON.parse(window.localStorage.getItem("acticle") || {} )
      acticle: JSON.parse(window.localStorage.getItem("acticle") || {}),
      // author:"",
    }
  },
  methods: {
    changeImg(index) {
      this.num = index
    },
    getsong(id) {
      this.axios
        .get(`https://apis.netstart.cn/one/radio/htmlcontent/${id}`)
        .then((res) => {
          // this.song = res.data.data.json_content.radio_url
          this.acticle = res.data.data
          // this.author = res.data.data.author_list[0].user_name
        })
    },
    updatetime(time) {
      console.log(Number(time));
      this.$refs.audio.currentTime = Number(time)
    }
  },

  computed: {
    ...mapState(["playingD", "palying"])
  },
  watch: {
    "$store.state.palying"() {
      // this.drawer = this.$store.state.drawerStatus;
      if (this.$store.state.palying) {
        this.$refs.audio.play()
      } else {
        this.$refs.audio.pause()
      }
    },
    "$store.state.playingD"() {
      // console.log(this.$store.state.playingD);
      this.getsong(this.$store.state.playingD)
    },
    acticle() {
      window.localStorage.setItem("acticle", JSON.stringify(this.acticle))
    }
  }
}
</script>
