<template>
    <div>
        <div class="top">
            <div class="return" @click="$router.go(-1)"><img
                    src="../assets/图标/com_taobao_tae_sdk_web_view_title_bar_back.9.png" alt=""></div>
            <img class="bg" src="../assets/图标/login_head.png" alt="">
            <img class="logo" src="../assets/图标/ali_login_one_logo.png" alt="">
        </div>

        <div class="login">
            <van-form @submit="onSubmit">
                <van-cell-group inset>
                    <van-field v-model="username" name="用户名" label="用户名" placeholder="用户名"
                        :rules="[{ required: true, message: '请填写用户名' }]" />
                    <van-field v-model="password" type="password" name="密码" label="密码" placeholder="密码"
                        :rules="[{ required: true, message: '请填写密码' }]" />
                </van-cell-group>
                <div style="margin: 16px;">
                    <van-button round block type="primary" native-type="submit">
                        登录
                    </van-button>
                </div>
            </van-form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            username: '',
            password: '',
            token: '',
        };
    },
    methods: {
        onSubmit(values) {
            console.log('submit', values);
            setTimeout(() => {
                // 假设是ajax的结果
                // let token = ''
                if (this.username === 'sake' && this.password == '12345') {
                    this.token = 'MaJ6bKsjj'
                } else {
                    this.token = ''
                }
                if (this.token == 'MaJ6bKsjj') {
                    localStorage.setItem('token', this.token)
                    this.$router.replace(this.$route.query.from)

                } else {
                    // 账号密码错误
                    this.username = ''
                    this.password = ''
                    window.alert("账号或者密码错误");
                }
            }, 1000);
        },
    },
};
</script>

<style lang="less" scoped>
.return {
    width: 20rem;
    height: 20rem;
    position: absolute;
    top: 10rem;
    left: 10rem;
}


.top {
    position: relative;

    .bg {}

    .logo {
        position: absolute;
        z-index: 611;
        top: 76px;
        left: 146px;
        width: 100rem;
    }
}

.van-button--primary {
    color: #fff;
    background-color: #a6a6a6;
    border: 1px solid #a6a6a6;
}

.login {
    margin-top: 30rem;
}
