<template>
    <div class="picPage">
        <div class="choosedata" @click="showxz = true">
            <div class="data" v-if="!data">{{whichdata? whichdata.substring(0,4) : yearmonth.substring(0,4)
            }}年{{whichdata? whichdata.substring(5,7) : time.substring(5,7) }}月</div>
            <div v-else>{{!data? whichdata.substring(0,4) : data.substring(0,4) }}年{{!data? whichdata.substring(5,7) :
            data.substring(5,7) }}月</div>
            <img style="width:10rem;height:10rem;" src="../assets/图标/arrow_down_black.png" alt="">
        </div>
        <div class="data-show" @click.stop="showxz=false" v-if="showxz">
            <van-datetime-picker v-model="currentDate" type="year-month" title="选择日期" :min-date="minDate"
                :max-date="maxDate" :formatter="formatter" ref="xz" confirm-button-text=" " cancel-button-text=" " />
            <p @click.stop="showxz = false; isquer = true">确认</p>
        </div>
        <div v-if="showlocading" class="locadingmask">
            <div class="locading"></div>
        </div>
        <van-list ref="vanlist" v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"
            @scroll.native="scrollHandler" style="height: 95vh; overflow: auto" v-if=" picing.length">
            <ul ref="testdiv" v-for="(month,index) in picing" :key="index">
                <van-divider>{{month?.data?.substring(5,7)}}月</van-divider>
                <FindPicItem v-for="(pic,index) in month.pics" :key="index" :pic="pic" />
            </ul>
        </van-list>
    </div>
</template>

<script>
import FindPicItem from '@/components/items/FindPicItem.vue'
// import FindPicItem from '@/components/items/FindPicItem.vue'
import { mapMutations, mapState } from "vuex";
export default {
    components: {
        FindPicItem,
    },
    data() {
        return {
            yearmonth: "",
            picing: [],
            minDate: new Date(2012, 0, 1),
            maxDate: new Date(2022, 9, 1),
            currentDate: new Date(),
            showxz: false,
            data: "",
            isDao: false,
            isquer: false,
            showlocading: false,

            readingOffsetHeight: [],
            whichdata: "",


            loading: false,
            finished: false,
        }
    },
    methods: {
        ...mapMutations(["nowtime"]),
        getpic(data) {
            // this.showlocading = true
            // let qing = 0;
            this.isDao = false
            this.axios
                .get(` https://apis.netstart.cn/one/find/bymonth/0/${data}`)
                .then((res) => {
                    // this.showlocading = false
                    this.picing = [...this.picing, { pics: res.data.data, data }]
                    // if (this.picing.length < 7) {
                    //     // this.prevMonth(data)  
                    //     this.prevMonth(data)
                    //     this.getpic(this.yearmonth)
                    // }
                    // if (qing > 0) {
                    //     this.isDao = true
                    // } else {
                    //     this.isDao = false
                    // }
                    this.loading = false;

                })
            // if (qing > 0) {
            //     this.isDao = true
            // } else {
            //     this.isDao = false
            // }
        },
        getnewpic(data) {
            this.isquer = false
            this.showlocading = true
            this.axios
                .get(` https://apis.netstart.cn/one/find/bymonth/0/${data}`)
                .then((res) => {
                    // this.picing = res.data.data
                    // this.picing = res.data.data
                    this.showlocading = false
                    this.picing = [{ pics: res.data.data, data }]
                })
        },
        onLoad() {
            // console.log("load");
            this.prevMonth(this.yearmonth)
            this.getpic(this.yearmonth)
        },
        formatter(type, val) {
            if (type === 'year') {
                return `${val}年`;
            } else if (type === 'month') {
                return `${val}月`;
            }
            return val;
        },
        show() {
            this.showxz = true
            console.log(111);
        },
        // pageScroll() {
        //     let scrollTop = document.documentElement.scrollTop
        //     let scrollHeight = document.documentElement.scrollHeight
        //     let clienHeight = document.documentElement.clientHeight
        //     // console.log(parseInt(scrollTop + clienHeight - scrollHeight));
        //     if (parseInt(scrollTop + clienHeight - scrollHeight) >= 0) {
        //         console.log("到底了");
        //         if (this.isDao == true) {
        //             this.prevMonth(this.yearmonth)
        //             this.getpic(this.yearmonth)
        //         }
        //     }
        // },
        prevMonth(yearmonth) {
            let currentDate = new Date(yearmonth);
            let lastDate = currentDate.setMonth(currentDate.getMonth() - 1); // 输出日期格式为毫秒形式1551398400000
            lastDate = new Date(lastDate);
            let lastYear = lastDate.getFullYear();
            let lastMonth = lastDate.getMonth() + 1; // 因日期中的月份表示为0-11，所以要显示正确的月份，需要 + 1
            if (lastMonth < 10) {
                lastMonth = "0" + lastMonth;
            }
            // this.data = lastYear + '-' + lastMonth;
            this.yearmonth = lastYear + '-' + lastMonth;
        },
        scrollHandler() {
            // console.log("huadong");
            // console.log(this.$refs.vanlist.$el.scrollTop);
            let index = [...this.readingOffsetHeight]
                .reverse()
                .findIndex((o) => this.$refs.vanlist.$el.scrollTop + 120 >= o);
            this.whichdata = this.picing[this.picing.length - 1 - index].data
            // console.log(this.picing.length - 1 - index);

            this.data = ""
            // console.log(this.picing[this.reading.length - 1 - index].data);
        },

    },
    computed: {
        ...mapState(["time"]),
    },
    created() {
        this.showlocading = true
        this.nowtime()
        this.yearmonth = this.time.substring(0, 7)
        this.getpic(this.yearmonth)
    },
    mounted() {
        this.showlocading = false
        // console.log(this.$refs.xz);
        // this.$refs.xz.cancel
        // console.log(window.onscroll);
        window.onscroll = this.pageScroll
        // if (!this.$refs.data.value) {
        this.$watch(() => {
            if (this.showxz) {
                this.data = this.$refs.xz.value
                    .toLocaleDateString()
                    .split("/")
                    .slice(0, 2)
                    .map(o => (o >= 10 ? o : "0" + o))
                    .join("-");
            }
            if (this.isquer) {
                // for (let i = 0; i < this.$refs.testdiv.children.length; i++) {
                //     this.$refs.testdiv.childNodes.forEach((o) => {
                //         // console.log(o.className);
                //         if (o.className == `h2`) {
                //             this.$refs.testdiv.removeChild(o);
                //         }
                //     });
                // }
                this.yearmonth = this.data
                this.picing = []
                // var childDiv1_1 = document.createElement("h2");
                // childDiv1_1.style.textAlign = "center"
                // childDiv1_1.style.fontSize = "14rem"
                // childDiv1_1.style.color = "#999"
                // childDiv1_1.style.width = "375rem"
                // childDiv1_1.className = "h2"
                // childDiv1_1.innerText = this.yearmonth.substring(5, 7) + '月'
                // this.$refs.testdiv.appendChild(childDiv1_1);
                this.getnewpic(this.data)
            }
        })
        // console.log(this.$refs.xz.data.value);
        // }

    },
    watch: {
        picing: function (newReading) {
            newReading;
            this.$nextTick(() => {
                // console.log("this.$refs.testdiv", this.$refs.testdiv);
                if (this.$refs.testdiv) {
                    this.readingOffsetHeight = this.$refs.testdiv.map((el) => el.offsetTop);
                }
                // console.log(this.$refs.testdiv);
            });
        },
    },

}
</script>

<style lang="less" scoped>
.picPage {
    position: relative;
}

.van-divider {
    width: 375rem;
}

.van-list {
    // &:nth-of-type(1){
    margin-top: 70rem;
    // }
}

.locadingmask {
    position: fixed;
    top: 40rem;
    left: 0;
    width: 100%;
    height: 87%;
    background-color: #fff;
    z-index: 999;

    .locading {
        width: 154rem;
        height: 155rem;
        background-image: url("../assets/微信图片_20221007141751.jpg");
        background-repeat: no-repeat;
        animation-name: xxx;
        animation-duration: 1s;
        margin: 300rem auto;
        animation-iteration-count: infinite;
        /* animation-timing-function: linear; */
        animation-timing-function: steps(13, end);

    }
}

ul {
    // margin-top: 70rem;
    // padding: 10rem;
    display: flex;
    flex-wrap: wrap;
    // margin-bottom: 40rem;
}

.van-datetime-picker {
    position: relative;
    // top: 37rem;
    top: 45rem;
    left: 0;

}

.choosedata {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 30rem;
    color: gray;
    line-height: 30rem;
    position: fixed;
    top: 40rem;
    left: 0;
    background-color: white;
}
.van-picker__title{
        color: #999;
    }

.data-show {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(29, 29, 29, 0.553);
    height: 100%;
    width: 100%;

    p {
        position: absolute;
        top: 296rem;
        left: 165rem;
        z-index: 30;
        text-align: center;
        font-size: 20rem;
        font-weight: bold;
    }
}
</style>