<template>
    <li class="read-item" @click="gotoactive(read.id,read.category)" :class="yearmonth">

        <div class="read-item-fl">
            <van-image width="55" height="55" lazy-load :src="read.cover" />
        </div>
        <div class="read-item-fr">
            <div class="read-item-title">{{read.title}}</div>
            <div class="read-item-author">{{read.subtitle}}</div>
        </div>
    </li>
</template>

<script>
import { stringify } from 'qs';

export default {
    props: {
        read: Object,
        yearmonth: stringify,
    },
    methods: {

        gotoactive(id, n) {
            this.$router.push({ path: '/HomeArticle', query: { id: id, index: n } })

        }
    }
}
</script>

