                <div class="name">{{$route.query.authorname}}</div>
                <div class="js">{{$route.query.authordesc}}</div>
                <div class="gz">
                    <div class="kk">关注</div>
                </div>
                <div class="fsn">{{$route.query.num}}关注</div>
            </div>
        </div>
        <div class="slie">
            <HomeContent v-for="(neirong,index) in  authorxi" :key="index" :neirong="neirong"
                :category="neirong.category" />
            <div class="kb">
            </div>
        </div>
    </div>
</template>

<script>
import HomeContent from '@/components/HomeContent.vue'
export default {
    components: {
        HomeContent,
    },
    data() {
        return {
            authorxi: [],
        }
    },
    methods: {
        getauthor(id) {
            this.axios
                .get(`https://apis.netstart.cn/one/author/works?author_id=${id}&page_num=0`)
                .then((res) => {
                    // this.showlocading = false
                    this.authorxi = res.data.data
                })
        }
    },
    created() {
        this.getauthor(this.$route.query.id)
    }
}
</script>

